.footer {
	padding-bottom: 56px;
	color: #fff;
	background-color: #181A21;
	&__inner {
		.footer__top & {
			align-items: center;
			justify-content: space-between;
			// flex-wrap: wrap
			display: flex; } }
	&__top {
		padding: 20px 0;
		background-color: #20232F; }
	&__logo {
		margin-right: 37px;
		width: 52px;
		flex-shrink: 0;
		img {
			max-width: 100%;
			display: block; } }
	&__contacts {
		margin-right: auto;
		flex-wrap: wrap;
		display: flex;
		font-size: 14px; }
	&__field {
		align-items: center;
		flex-wrap: wrap;
		display: flex;
		&:not(:last-child) {
			margin-right: 30px; } }
	&__label {
		margin-right: 10px;
		font-size: 12px; }
	&__item {
		margin-right: 20px; }
	&__soc {
		flex-shrink: 0; }
	&__middle {
		padding-top: 60px;
		font-size: 18px;
		line-height: 1.5;
		font-weight: 400; }
	&__row {}
	&__nav {
		ul {
			column-count: 2;
			column-gap: 20px;
			display: block; } }
	.nav {
		font-weight: 400;
		li {
			margin: 0 0 15px;
			width: 100%;
			display: inline-block;
			&:not(:last-child) {
				margin-right: 0; } } }
	&__col {}
	&__title {
		margin-bottom: 40px;
		@include f(21px,600,1.2); }
	&__col {}
	&__buttons {
		padding-bottom: 20px;
		max-width: 250px;
		height: 100%;
		justify-content: flex-end;
		display: flex;
		flex-direction: column;
		margin-left: auto;
		.btn {
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__bottom {
		padding-top: 60px;
		font-weight: 400;
		font-size: 18px; } }
